export default {
	data() {
		return {
			list:[ "前端","后台","听到","CSS","前端","后台","听到","CSS" ]
		}
	},
	methods: {
		// 点击触发下拉刷新
		pulldown(){
			uni.startPullDownRefresh()
		}
	},
	onShow: () => {
		console.log("页面显示了")
	},
	onHide: () => {
		console.log("页面隐藏了")
	},
	onPullDownRefresh(){
		console.log("页面下拉刷新了")
		// 关闭下拉刷新
		setTimeout(()=>{
			this.list = ['1','2','3','4']
			uni.stopPullDownRefresh()
		},1000)
	},
	onReachBottom(){
		setTimeout(()=>{
			console.log("页面触底了")
			// this.list = ['1','2','3','4']
			this.list = [...this.list,...["前端","后台","html","CSS"]]
		},500)
	}
}
